export default {
  name: "EventCard",
  props: {},
  emits: ["emit-logout"],
  data() {
    return {
      thumbStyle: {
        right: "4px",
        borderRadius: "5px",
        backgroundColor: "#027be3",
        width: "5px",
        opacity: 0.75,
      },
      barStyle: {
        right: "2px",
        borderRadius: "9px",
        backgroundColor: "#027be3",
        width: "9px",
        opacity: 0.2,
      },
    };
  },
  computed: {},
  methods: {
    mask(text) {
      const limit = 15;
      if (text.length > limit) return text.substring(0, limit) + "...";
      return text;
    },
  },
};
